
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Modal from '../order/modal'
export default {
name: 'Invoice',
components: {
XHeader,
XButton,
XInput,
XDialog,
XSwitch,
Modal
},
directives: {
TransferDom
},
data () {
return {
modalStatus: {},
invoiceHeaderValue: '',
taxpayerValue: '',
chooseSelect: true,
chooseDefault: false,
cardFlag: true
}
},
methods: {
btnClick () {
let invoicerReg = /^(?!(?:\d+)$)[\dA-Z]{18}$/
this.cardFlag === true ? this.taxpayerValue = '' : this.taxpayerValue = this.taxpayerValue
if (this.cardFlag) {
this.modalStatus = {
success_status: true,
dialogTitle: '保存成功'
}
this.axios.post('/order/add-line-order-invoice', {
invoice_type: '1',
invoice_header_type: '2',
invoice_header: this.invoiceHeaderValue,
invoice_content: '明细',
invoicer_card: this.taxpayerValue
}, (res) => {
console.log(res)
if (res.code === 200) {
sessionStorage.setItem('invoice_id', res.data.invoice_id)
}
})
} else {
if (invoicerReg.test(this.taxpayerValue)) {
this.modalStatus = {
success_status: true,
dialogTitle: '保存成功'
}
this.axios.post('/order/add-line-order-invoice', {
invoice_type: '1',
invoice_header_type: '2',
invoice_header: this.invoiceHeaderValue,
invoice_content: '明细',
invoicer_card: this.taxpayerValue
}, (res) => {
console.log(res)
if (res.code === 200) {
sessionStorage.setItem('invoice_id', res.data.invoice_id)
}
})
} else {
console.log(this.taxpayerValue)
this.modalStatus = {
error_status: true,
dialogTitle: '纳税人识别号输入不正确',
dialogContent: '请重新输入'
}
}
}
},
personalClick () {         // 个人 点击
this.chooseSelect = true
this.chooseDefault = false
this.cardFlag = true
},
unitClick () {            // 单位 点击
this.chooseSelect = false
this.chooseDefault = true
this.cardFlag = false
}
},
activated () {
this.chooseSelect = true
this.chooseDefault = false
this.cardFlag = true
if (sessionStorage.getItem('invoice_id')) {
this.axios.get('/order/get-line-order-invoice-data', {
invoice_id: sessionStorage.getItem('invoice_id')
}, (res) => {
this.invoiceHeaderValue = res.data.invoice_header
this.taxpayerValue = res.data.invoicer_card
})
} else {
this.invoiceHeaderValue = ''
this.taxpayerValue = ''
}
}
}



