
import Msg from 'vux/src/components/msg/index.vue'


export default {
name: 'PaySuccess',
components: {
Msg
},
data () {
return {
number: 5
}
},
mounted () {
const timeInterval = setInterval(() => {
this.number--
console.log(this.number)
if (this.number < 1) {
clearInterval(timeInterval)
this.$router.push({path: '/'})
}
}, 1000)
}
}



