
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Modal from '../order/modal'
export default {
name: 'Address',
components: {
XHeader,
Group,
XInput,
XAddress,
XTextarea,
XButton,
XDialog,
XSwitch,
Modal
},
directives: {
TransferDom
},
activated () {
if (sessionStorage.getItem('address_id')) {
this.axios.get('/order/get-line-receive-address', {
address_id: sessionStorage.getItem('address_id')
}, (res) => {
console.log(res)
this.consignee = res.data.consignee
this.phoneNumber = res.data.mobile
this.addressValue = res.data.district.split(',')
this.detailAddress = res.data.region
})
} else {
this.consignee = ''
this.phoneNumber = ''
this.addressValue = ['四川省', '成都市', '锦江区']
this.detailAddress = ''
}
},
data () {
return {
modalStatus: {},
requiredStatus: false,
phoneStatus: false,
addressStatus: false,
consignee: '',
phoneNumber: '',
addressValue: ['四川省', '成都市', '锦江区'],
detailAddress: '',
addressData: ChinaAddressV4Data
}
},
methods: {
btnClick () {
let phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
if (phoneReg.test(this.phoneNumber)) {
this.modalStatus = {
success_status: true,
dialogTitle: '保存成功'
}
this.axios.post('/order/add-line-receive-address', {
consignee: this.consignee,
mobile: this.phoneNumber,
district_code: this.addressValue[2],
region: this.detailAddress
}, (res) => {
console.log(res)
if (res.code === 200) {
sessionStorage.setItem('address_id', res.data.address_id)
}
})
} else {
this.modalStatus = {
error_status: true,
dialogTitle: '手机号输入不正确',
dialogContent: '请重新输入'
}
}
},
hide () {                 // 关闭模态框时，跳转回订单页
this.$router.go(-1)
}
}
}



