
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import ShopDetails from './shop_details'
import Modal from '../order/modal'
export default {
name: 'Order',
components: {
XHeader,
Group,
Cell,
CellBox,
XTextarea,
ShopDetails,
Modal
},
data () {
return {
shopObj: {}, // 店铺商品信息
modalStatus: {}, // 模态框
consigneeFlag: true, // 有无收货地址状态
shop_name: '', // 店铺名称
goods_name: '', // 商品名称
cover_image: '', // 封面图
platform_price: '', // 平台价 优惠价
goods_num: '',  // 数量
market_price: '', // 市场价
freight: '', // 运费
allCost: '', // 需付款金额
consignee: '', // 收货人
head_number: '', // 手机号前3位
foot_number: '', // 手机号后4位
addressValue: '', // 所属区域
adCode: '', // 所属区域的区号
detailAddress: '', // 详细地址
invoice_header: '', // 抬头
buyer_message: '', // 留言
checked_flag: true, // 是否勾选“商城购买协议”
paymentMethod: '', // 支付方式
openID: '' 
}
},
methods: {
toPay () {
if (!this.consigneeFlag) {      // 已填写收货地址
this.axios.get('/order/judge-address', {
goods_id: this.$route.query.goods_id,
district_code: this.adCode
}, (res) => {
console.log(res)
if (res.code === 200) {
if (this.checked_flag) {                  // 已勾选协议
if (sessionStorage.getItem('wxCodeFlag')) {               // 微信支付
console.log('微信支付')
this.axios.post('/order/order-line-wx-pay', {
order_price: this.allCost,
goods_id: this.$route.query.goods_id,
goods_num: this.$route.query.goods_num,
address_id: sessionStorage.getItem('address_id'),
invoice_id: sessionStorage.getItem('invoice_id'),
freight: this.freight,
buyer_message: this.buyer_message,
open_id: sessionStorage.getItem('openID')
}, (res) => {
console.log('openID返回值')
console.log(res)
function onBridgeReady () {
/* global WeixinJSBridge:true */
WeixinJSBridge.invoke(
'getBrandWCPayRequest', {
'appId': res.data.appId,                 // 公众号名称，由商户传入
'timeStamp': res.data.timeStamp,         // 时间戳，自1970年以来的秒数
'nonceStr': res.data.nonceStr,          // 随机串
'package': res.data.package,
'signType': res.data.signType,          // 微信签名方式：
'paySign': res.data.paySign             // 微信签名
},
function (res) {
console.log(res)
if (res.err_msg === 'get_brand_wcpay_request:ok') {
this.$router.push({path: '/'})
}     // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
}
)
}
if (typeof WeixinJSBridge === 'undefined') {
if (document.addEventListener) {
document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false)
} else if (document.attachEvent) {
document.attachEvent('WeixinJSBridgeReady', onBridgeReady)
document.attachEvent('onWeixinJSBridgeReady', onBridgeReady)
}
} else {
onBridgeReady()
}
})
} else {        // 支付宝支付
console.log('支付宝支付 ')
this.axios.post('/order/order-line-ali-pay', {
order_price: this.allCost,
goods_id: this.$route.query.goods_id,
goods_num: this.$route.query.goods_num,
address_id: sessionStorage.getItem('address_id'),
invoice_id: sessionStorage.getItem('invoice_id'),
freight: this.freight,
buyer_message: this.buyer_message
}, (res) => {
console.log(res)
const div = document.createElement('div') 
div.innerHTML = res 
document.body.appendChild(div)
document.forms[0].submit()
sessionStorage.removeItem('invoice_id')
})
}
} else {
this.modalStatus = {
error_status: true,
dialogTitle: '请先勾选购买协议'
}
}
} else {
this.modalStatus = {            // 所填区域不在配送范围内
error_status: true,
dialogContent: '你好，你购买的［' + this.goods_name + '］属于区域销售商品，你所选择的收货地址不在配送范围内，请更换商品或收货地址！'
}
}
})
} else {                        // 未填写收货地址
this.modalStatus = {
error_status: true,
dialogTitle: '请填写收货地址'
}
}
}
},
activated () {

this.axios.get('/order/get-line-goods-info', {
goods_id: this.$route.query.goods_id,
goods_num: this.$route.query.goods_num
}, (res) => {
console.log(res)
this.shop_name = res.data.shop_name 
this.goods_name = res.data.title 
this.cover_image = res.data.cover_image 
this.platform_price = res.data.platform_price 
this.goods_num = res.data.goods_num 
this.market_price = res.data.market_price 
this.freight = res.data.freight 
this.allCost = res.data.allCost 
this.shopObj = {
shop_name: this.shop_name,
title: this.goods_name,
cover_image: this.cover_image,
platform_price: this.platform_price,
goods_num: this.goods_num
}
})

if (sessionStorage.getItem('address_id')) {
this.consigneeFlag = false
this.axios.get('/order/get-line-receive-address', {
address_id: sessionStorage.getItem('address_id')
}, (res) => {
console.log(res)
this.consignee = res.data.consignee
this.phoneNumber = res.data.mobile
this.head_number = this.phoneNumber.substring(0, 3)
this.foot_number = this.phoneNumber.substring(7)
this.addressValue = res.data.district.replace(/,/g, '')
this.detailAddress = res.data.region
this.adCode = res.data.adCode
})
} else {
this.consigneeFlag = true
}

if (sessionStorage.getItem('invoice_id')) {
this.axios.get('/order/get-line-order-invoice-data', {
invoice_id: sessionStorage.getItem('invoice_id')
}, (res) => {
console.log(res)
this.invoice_header = res.data.invoice_header
})
}

this.axios.get('/order/iswxlogin', {}, (res) => {
res.code === 200 ? this.paymentMethod = true : this.paymentMethod = false
})
}
}



