
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import vHeader from '@/components/HeaderSearch'
import GoodsList from '@/components/GoodsList'
import OfflineAlert from '@/components/OfflineAlert'

export default {
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
vHeader,
GoodsList,
OfflineAlert
},
data () {
return {
isPosition: false,             // tab是否定位到 header 下面
isLoading: false,              // 是否在加载状态
isShowAlert: false,            // 是否显示线下体验店弹窗
loadingText: '加载中...',      // 加载提示信息，默认为加载中...
totalPage: 0,                  // 总页数
tabActive: 0,                  // 默认选中店铺首页
tabHeight: 44,                 // tab 默认最小高度为 44 像素
carousel: [],                  // 店铺首页轮播
storeData: {},                 // 店铺信息
recommendParams: {             // 店铺推荐商品请求参数
supplier_id: this.$route.params.id,
page: 1
},
recommendGoods: [],            // 推荐商品列表数据
uid: null,                    // 商家对应用户ID
offlineInfo: {                 // 线下体验店弹窗信息
address: '',
phone: '',
desc: '线下体验店是与艾特生活线上平台相配套的落地服务，负责接纳客户业务咨询、为消费者提供智能家居体验的区域服务中心'
},
sortName: 'sold_number',              // tab排序名称  sold_number(销量优先)  platform_price(价格) favourable_comment_rate(好评率)
platformPriceSortNum: 4,              // 价格排序方式   3：降序 4：升序
favourableCommentRateSortNum: 4,      // 好评率排序方式   3：降序 4：升序
allGoodsParams: {     // 全部商品请求参数
supplier_id: this.$route.params.id,
page: 1,
'sort[]': 'sold_number:3',
size: 12
},
allGoodsData: []      
}
},
created () {
let tabIndex = sessionStorage.getItem('storeIndex')     
if (tabIndex !== null) {
this.tabActive = parseInt(tabIndex)
}
this.getStoreData()
console.log(typeof tabIndex, tabIndex)
if (this.tabActive === 0) {      // 判断选项卡选中状态，按状态请求数据
this.getStoreShopGoods()
} else {
this.getAllGoodsData()
}
},
mounted () {
window.addEventListener('scroll', this.handleScroll)




},
beforeDestroy () {
window.removeEventListener('scroll', this.handleScroll)
},
methods: {
onClickTab (index) {
this.tabActive = index
sessionStorage.setItem('storeIndex', index)     
if (index === 0) {

this.tabHeight = 44
this.recommendParams.page = 1     
this.recommendGoods = []      
this.getStoreShopGoods()
} else {

this.tabHeight = 88
this.allGoodsParams.page = 1      
this.allGoodsData = []      
this.getAllGoodsData()
}
},
getStoreShopGoods () {     // 请求店铺首页推荐商品
this.axios.get('/supplier/recommend-second', this.recommendParams, res => {
console.log(res, '店铺推荐商品')
let data = res.data
this.recommendGoods = this.recommendGoods.concat(data.recommend_second)
this.totalPage = Math.ceil(data.total / 12)     
this.isLoading = false      
})
},
getStoreData () {     // 请求店铺数据
this.axios.get('/supplier/index', {supplier_id: this.$route.params.id}, res => {
console.log(res, '店铺首页数据')
let data = res.data.index
this.storeData = data
this.carousel = data.carousel.map(item => {
return {
id: item.id,
url: '/good-detail/' + item.url,
img: item.image
}
})
this.offlineInfo.address = data.district
this.offlineInfo.phone = data.line_supplier_mobile
this.uid = data.supplier_uid
})
},
recordClicks (id) {
this.axios.post('/mall/recommend-click-record-supplier', {recommend_id: id})
},
recordScroll (id) {
this.axios.post('/mall/recommend-click-record-supplier', {recommend_id: id})
let scrollTop = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset     
sessionStorage.setItem('storePos', scrollTop)      
},
isShow (bool) {     // 线下体验店弹窗
this.isShowAlert = bool     
},
getAllGoodsData () {
this.axios.get('/supplier/goods', this.allGoodsParams, res => {
console.log(res, '全部商品列表')
let data = res.data
this.allGoodsData = this.allGoodsData.concat(data.supplier_goods)
this.totalPage = Math.ceil(data.total / 12)     
this.isLoading = false      
})
},
tabHandle (str) {
this.sortName = str
switch (str) {      // 只有在价格和好评率会做排序
case 'platform_price':

this.platformPriceSortNum = this.platformPriceSortNum === 4 ? 3 : 4     
this.favourableCommentRateSortNum = 4                                   
this.allGoodsParams['sort[]'] = this.sortName + ':' + this.platformPriceSortNum
break
case 'favourable_comment_rate':

this.favourableCommentRateSortNum = this.favourableCommentRateSortNum === 4 ? 3 : 4     
this.platformPriceSortNum = 4                                                           
this.allGoodsParams['sort[]'] = this.sortName + ':' + this.favourableCommentRateSortNum
break
case 'sold_number':
this.allGoodsParams['sort[]'] = this.sortName + ':' + 3
}
this.allGoodsParams.page = 1      
this.allGoodsData = []      
this.getAllGoodsData()
},
handleScroll () {
let tabTop = document.querySelector('.tab-group').getBoundingClientRect().top     
console.log(tabTop)
this.isPosition = tabTop <= 46      
if (this.isLoading) return    
let node, top, nodeTop
if (this.tabActive === 0) {     // 判断 tab 卡 选中的是否为店铺首页
node = document.querySelector('.store-goods-item:last-child')     
} else {
node = document.querySelector('.goods-item:last-child')     
}
top = document.documentElement.clientHeight     
nodeTop = node.getBoundingClientRect().top + 100      
console.log(nodeTop, top)
if (nodeTop <= top) {
this.isLoading = true           
if (this.tabActive === 0) {
if (this.recommendParams.page < this.totalPage) {      // 判断当前页是否小于最后一页
this.loadingText = '加载中...'
this.recommendParams.page++     
this.getStoreShopGoods()      
} else {
this.loadingText = '没有更多数据了'
}
} else {
if (this.allGoodsParams.page < this.totalPage) {      // 判断当前页是否小于最后一页
this.loadingText = '加载中...'
this.allGoodsParams.page++     
this.getAllGoodsData()        
} else {
this.loadingText = '没有更多数据了'
}
}
}
}
}
}



