
import Previewer from 'vux/src/components/previewer/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'

export default {
name: 'userComment',
components: {
Previewer,
Flexbox,
FlexboxItem,
XImg
},
props: ['userIcon', 'userName', 'commentLevel', 'commentDate', 'content', 'images', 'reply'],
data () {
return {
previewerList: [],
default_pic: require('../../assets/images/default_pic.png') 
}
},
methods: {

show (index) {
this.$refs.previewer.show(index)
}
},
filters: {

stringSubstr (value) {


if (value.length <= 10) {
return value
} else {
return value.substr(0, 10) + '...'
}
}
}
}



