
import Alert from 'vux/src/components/alert/index.vue'


export default {
components: {
Alert
},
props: {
isOffline: {  // 判断是否是线下体验店，true 是体验店； false 是体验商品
type: Boolean,
default: true
},
show: { // 是否显示弹窗，默认不显示
type: Boolean,
default: false
},
/**
* 体验店弹框信息
* 对象请包含
* {
*   address: '',
*   phone: '',
*   desc: ''
* }
*/
offlineInfo: {
type: Object,
default: {}
}
},
methods: {
showFun () {
this.$emit('isShow', false)
}
}
}



