
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import HeaderSearch from '@/components/HeaderSearch'
import GoodsList from '@/components/GoodsList'

export default {
components: {
Popup,
Group,
Cell,
Checker,
CheckerItem,
GoodsList,
HeaderSearch
},
data () {
return {
isMoreOpen: false,      // 头部更多选项是否显示
isModalOpen: false,     // 模态框是否显示
isStyleOpen: false,     // 风格是否显示
isSeriesOpen: false,    // 系列是否显示
isBrandOpen: false,     // 品牌是否显示
isLoading: false,       // 判断是否在加载状态
loadingText: '加载中...',     // 加载提示信息，默认为加载中...
sortName: 'sold_number',      // tab排序名称  sold_number(销量优先)  platform_price(价格) favourable_comment_rate(好评率)
platformPriceSortNum: 4,      // 价格排序方式   3：降序 4：升序
favourableCommentRateSortNum: 4,      // 好评率排序方式   3：降序 4：升序
styleData: [],          // 筛选模态框风格数据
seriesData: [],         // 筛选模态框系列数据
brandData: [],          // 筛选模态框品牌数据
goodsListData: [],      // 商品列表数据
totalPage: 0,               // 商品列表数据总页数
goodsListParams: {
district_code: 510100,        // 城市 code
category_id: null,           // 3级分类ID
'sort[]': 'sold_number:3',   // 默认按照销量优先降序排序
page: 1,
brand_id: null,     // 筛选品牌ID
style_id: null,     // 筛选风格ID
series_id: null     
},
filterParams: {     // 模态框筛选
styleParams: '',  // 筛选模态框里，选中的风格
seriesParams: '', // 筛选模态框里，选中的系列
brandParams: [],  // 筛选模态框里，选中的品牌（可多选）
priceMin: '',     // 最低价
priceMax: ''      
},
minWatch: null,     // 最小价格监听
maxWatch: null      
}
},
methods: {
tabHandle (str) {
this.sortName = str
switch (str) {      // 只有在价格和好评率会做排序
case 'platform_price':

this.platformPriceSortNum = this.platformPriceSortNum === 4 ? 3 : 4     
this.favourableCommentRateSortNum = 4                                   
this.goodsListParams['sort[]'] = this.sortName + ':' + this.platformPriceSortNum
this.goodsListParams.page = 1
break
case 'favourable_comment_rate':

this.favourableCommentRateSortNum = this.favourableCommentRateSortNum === 4 ? 3 : 4     
this.platformPriceSortNum = 4                                                           
this.goodsListParams['sort[]'] = this.sortName + ':' + this.favourableCommentRateSortNum
this.goodsListParams.page = 1
break
case 'sold_number':
this.goodsListParams['sort[]'] = this.sortName + ':' + 3
this.goodsListParams.page = 1
}
this.goodsListData = []     
this.getGoodsList()
},
getGoodsList () {     // 商品列表数据请求
this.goodsListParams.category_id = this.$route.params.id      
this.axios.get('/mall/category-goods', this.goodsListParams, res => {
console.log(res, '商品列表数据')
let data = res.data
this.goodsListData = data.category_goods
this.totalPage = Math.ceil(data.total / 12)     
this.isLoading = false      
})
},
getMoreGoodsList () {     // 获取更多商品数据
this.goodsListParams.category_id = this.$route.params.id      
this.axios.get('/mall/category-goods', this.goodsListParams, res => {
let data = res.data
this.goodsListData = this.goodsListData.concat(data.category_goods)
this.totalPage = Math.ceil(data.total / 12)     
this.isLoading = false      
})
},
filterModalData () {      // 模态框数据请求
if (typeof this.minWatch === 'function') {
this.minWatch()     
}
if (typeof this.maxWatch === 'function') {
this.maxWatch()
}
this.minWatch = this.$watch('filterParams.priceMin', function (n, o) {
if (n === o) return
let reg = /^[0-9]{0,8}$/
if (!reg.test(n)) {
this.filterParams.priceMin = o
}
})
this.maxWatch = this.$watch('filterParams.priceMax', function (n, o) {
if (n === o) return
let reg = /^[0-9]{0,8}$/
if (!reg.test(n)) {
this.filterParams.priceMax = o
}
})
this.isModalOpen = true     
this.axios.get('/mall/category-brands-styles-series', {category_id: this.$route.params.id}, res => {
console.log(res, '风格、系列和品牌')
let data = res.data.category_brands_styles_series
this.styleData = data.styles
this.seriesData = data.series
this.brandData = data.brands
})
},
filterFinish () {     // 完成筛选
if (this.filterParams.priceMin !== '' && this.filterParams.priceMax !== '') {     // 判断价格最小值和最大值是否都有值
if (this.filterParams.priceMin * 100 > this.filterParams.priceMax * 100) {

let tempPrice = this.filterParams.priceMax
this.filterParams.priceMax = this.filterParams.priceMin
this.filterParams.priceMin = tempPrice
}
}
this.goodsListParams.platform_price_min = this.filterParams.priceMin * 100
this.goodsListParams.platform_price_max = this.filterParams.priceMax * 100
this.goodsListParams.brand_id = this.filterParams.brandParams.join(',')       
this.goodsListParams.style_id = this.filterParams.styleParams                 
this.goodsListParams.series_id = this.filterParams.seriesParams               
this.goodsListParams.page = 1
this.isModalOpen = false      
this.goodsListData = []       
this.getGoodsList()           
},
filterReset () {      // 重置筛选
this.filterParams = {     // 模态框筛选
styleParams: '',      // 筛选模态框里，选中的风格
seriesParams: '',     // 筛选模态框里，选中的系列
brandParams: [],      // 筛选模态框里，选中的品牌（可多选）
priceMin: '',         // 最低价
priceMax: ''          
}
this.goodsListParams.platform_price_min = null      
this.goodsListParams.platform_price_max = null      
this.goodsListParams.brand_id = null                
this.goodsListParams.style_id = null                
this.goodsListParams.series_id = null               
this.goodsListParams.page = 1
this.isModalOpen = false      
this.goodsListData = []       
this.getGoodsList()           
},
handleScroll () {
let scrollTop = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset     
sessionStorage.setItem('pos', scrollTop)      
if (this.isLoading) return    
let node = document.querySelector('.goods-item:last-child')     
let top = document.documentElement.clientHeight     
let nodeTop = node.getBoundingClientRect().top + 100      
console.log(nodeTop, top)
if (nodeTop <= top) {
this.isLoading = true           
if (this.goodsListParams.page < this.totalPage) {      // 判断当前页是否小于最后一页
this.loadingText = '加载中...'
this.goodsListParams.page++     
this.getMoreGoodsList()
} else {
this.loadingText = '没有更多数据了'
}
}
}
},
created () {
this.getGoodsList()
},
mounted () {
window.addEventListener('scroll', this.handleScroll)
},
beforeDestroy () {
window.removeEventListener('scroll', this.handleScroll)
}
}



