
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'


export default {
components: {
XHeader,
Search
},
props: {
goLink: {       // backWay 为 true 时，跳转的链接
type: String,
default: '/'
},
goParams: {     // 跳转指定页面的参数
type: Object,
default: function () {
return {}
}
},
search: '',     // true 为显示 搜索框
headTitle: '',  // 标题
pop: '',        // true为显示 返回首页
backWay: ''     
},
data () {
return {
isShow: false
}
},
methods: {
goHome () {
console.log(this.goLink, this.goParams)
this.$router.replace({
path: this.goLink,
params: this.goParams
})
}
}
}



